body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #f7f8fb;
}

.main {
  padding: 40px;
}

.employee {
  background: #fff;
  padding: 20px;
  margin-bottom: 10px;
}

.employee:hover {
  box-shadow: 1px 8px 25px #e2e2e2;
}

.employee img {
  height: 32px;
  border-radius: 25px;
}

.name {
  font-weight: 700;
}

.name,
.email,
.text {
  font-size: 14px;
}

.text {
  font-weight: 700;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.menu {
  cursor: pointer;
}

.grey {
  color: #bbb;
}

.header {
  padding-bottom: 15px;
  font-size: 14px;
  font-weight: 700;
}

.load-more {
  padding: 30px;
}

